<template>
	<div class="warehouse">
		<ul>
			<li @click="choose(item)" v-for="(item, index) in shichang" >
				<h1>{{item.name}}</h1>
				<div class="price">{{item.price | currency}}</div>
			</li>
		</ul>
		<tanchu :list='list' :close='close' v-show="tanchushow"></tanchu>
	</div>
</template>

<script>
	import tanchu from './tanchu.vue'

	export default{
		props:{
			shichang:{
				type:Array
			}
		},
		data(){
			return{
				list:[],
				tanchushow:false
			}
		},
		computed:{
			funds(){
				return this.$store.getters.funds
			}
		},
		methods:{
			choose(item){
				this.list = item;
				this.tanchushow = true;
			},
			close(){
				this.tanchushow = false
			}
		},
		components:{
			tanchu
		}
	}
</script>
