<template>
	<div class="gupiao">
		<h1>纳斯达克股票交易所</h1>
		<div class="keepalive">
			<button @click="comeon = 'gupiao-warehouse'" :class="{active: comeon =='gupiao-warehouse'}">仓库</button>
			<button @click="comeon = 'gupiao-nav'" :class="{active: comeon =='gupiao-nav'}" >股票市场</button>
			<span>我的资产：  {{funds | currency}}</span>
			<div class="box">
				<keep-alive>
					<component :is="comeon" :shichang="shichang" :stock="stocks"></component>
				</keep-alive>
			</div>
			<!-- <gupiao-warehouse></gupiao-warehouse> -->
		</div>
		<div class="footer">
			<button @click="endDay()">结束交易日</button>
			<!-- <button>保存进度</button>
			<button>载入进度</button> -->
		</div>
	</div>
</template>

<script>
	import warehouse from '../components/gupiao/warehouse.vue'
	import gupiaonav from '../components/gupiao/gupiaonav.vue'
	import {mapActions} from 'vuex'


	export default{
		data(){
			return{
				comeon:'gupiao-warehouse',
			}
		},
		components:{
			'gupiao-warehouse':warehouse,
			'gupiao-nav':gupiaonav
		},
		computed:{
			shichang(){
				return this.$store.getters.stocks
			},
			funds() {
                return this.$store.getters.funds;
            },
            stocks(){
            	return this.$store.getters.stockPortfolio
            }
		},
		methods:{
			...mapActions([
				'randomizeStocks'
			]),
			endDay(){
				console.log('12')
				this.randomizeStocks()
			}
		},
		created(){
			this.$store.dispatch('initStocks')
		}
	}
</script>

<style lang="scss" scoped>
	.gupiao{
		padding: 10% 4%;
		box-sizing: border-box;
		h1{
			margin: .30rem 0;
			font-size: .44rem;
			font-weight: 700;
			text-align: center;
		}
		.keepalive{
			button{
				background: transparent;
				margin-right:.2rem;
				font-size: .32rem;
				height: .5rem;
				color: #333;
				margin-bottom: .1rem;
			}
			span{
				float: right;
				height: .5rem;
				line-height: .5rem;
				font-size: .26rem;
				color: rgba(0,0,0,.7);
			}
			.active{
				color: green;
			}
			.box{
				width: 100%;
				padding-top: .2rem;
				height: 6rem;
			}
		}
		.footer{
			button{
				padding:0 .2rem;
				height: .6rem;
				line-height: .6rem;
				background:green;
				margin-right: .4rem;
				color: white;
			}
		}
	}
</style>