<template>
	<div class="tanchu" >
		<div class="bg" @click="close()"></div>
		<div class="box">
			<div class="head">
				<div class="left">{{list.name}}</div>
				<div class="right">{{list.price | currency}}</div>
			</div>
			<div class="mid">
				<p v-if="!quantity">您确定要购买
					<span>{{list.name}}</span>
					的股票嘛
				</p>
				<p v-else>若购买{{quantity}}股，您的资产将还剩
					<span>{{jisuan | currency}}</span>
				元
				</p>
			</div>
			<input v-model="quantity" type="text" autofocus="autofocus">	
			<div class="button">
				<button  v-if="insufficientFund && quantity > 0 && !Number.isInteger(quantity)" @click="buy()">立即购买</button>
			</div>
			<div class="foot">
				<img src="../../assets/images/gold.png">
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		props:{
			list:{
				type:Object
			},
			close:{
				type:Function
			}
		},
		data(){
			return{
				quantity:'',
			}
		},
		methods:{
			buy(){
				const order = {
					stockId: this.list.id,
					quantity: this.quantity,
					stockPrice: this.list.price
				}
				console.log(order)
				this.$store.dispatch('buyStock', order)
				this.quantity = ''
			}
		},
		computed:{
			jisuan(){
				return this.$store.getters.funds - this.quantity * this.list.price 
			},
			insufficientFund(){
				return this.quantity * this.list.price < this.$store.getters.funds;
			}
		}
	}
</script>

<style lang="scss">
	.tanchu{
		.bg{
			position:absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100vh;
			background: rgba(0,0,0,.6);
			z-index: 2;
		}
		.box{
			position: absolute;
			width: 70%;
			height: 5rem;
			background: white;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			margin: auto;
			overflow: hidden;
			z-index: 8;
			.head{
				width: 100%;
				height: .8rem;
				padding: .2rem;
				box-sizing: border-box;
				font-size: .32rem;
				.left{
					float: left;
					font-weight: 700;
				}
				.right{
					float: right;
				}
			}
			.mid{
				padding-left: .2rem;
				p{	
					line-height: .4rem;
					span{
						color: #f03;
					}
				}
			}
			input{
				display: block;
				margin: 0 auto;
				margin-bottom: .3rem;
				width: 90%;
				height: .7rem;
				line-height: .1rem;
				font-size: .32rem;
				text-align: center;
				border-bottom:.04rem solid green;
				caret-color:green;
				color: green;
			}
			.button{
				width: 100%;
				height: .7rem;
				text-align: center;
				button{
					width: 2rem;
					padding:.1rem;
					font-size: .32rem;
					text-align: center;
					background:green;
					color: white;
				}
			}
			.foot{
				width: 100%;
				bottom:0;
				img{
					width: 100%;
				}
			}
		}
	}
</style>