<template>
	<div class="warehouse">
		<ul v-if="stock.length">
			<li v-for="(item, index) in stock" @click="choose(item)">
				<h1>{{item.name}}</h1>
				<span>{{item.quantity}}股</span>
				<div class="price">{{item.price | currency}}</div>
			</li>
			
		</ul>
		<ul v-else>
			<li class="tishi">
				<h1>您还没有购买股票哦！</h1>
			</li>
		</ul>
		<maichu :list='list' :close='close' v-show="maichushow"></maichu>
	</div>
</template>

<script>
	import maichu from './maichu.vue'

	export default{
		props:['stock'],
		data(){
			return{
				quantity:'',
				maichushow:false,
				list:''
			}
		},
		methods:{
			choose(item){
				this.list = item;
				this.maichushow = true
			},
			close(){
				this.maichushow = false
			}
		},
		computed:{

			/*sto(){
				return this.$store.getters.stockPortfolio
			}*/
		},
		components:{
			maichu
		}
	}
</script>

